jQuery( document ).ready( ( $ ) => {
  //gravity-forms reload
  window.wld_gform_reload = [];

  function WLD_GForm_Reload( $gform ) {
    this.$parent  = $gform.parent();
    this.html     = this.$parent.html();
    this.gform_id = parseInt( $gform.attr( 'id' ).match( /\d+$/ )[0] );

    this.reload_form = function() {
      var $message = this.$parent.find( '#gform_confirmation_wrapper_' + this.gform_id );

      this.$parent.html( this.html );
      this.$parent.append( $message );

      $( document ).trigger( 'gform_post_render', [this.gform_id, 0] );
    };

    this.init = function() {
      var _this = this;

      $( document ).bind( 'gform_confirmation_loaded', function( event, gform_id ) {
        if ( gform_id === _this.gform_id ) {
          _this.reload_form();
        }
      } );
    };

    this.init();

    return this;
  }

  $( '.gform_wrapper' ).each( function() {
    window.wld_gform_reload.push( new WLD_GForm_Reload( $( this ) ) );
  } );

  jQuery( document ).on( 'gform_post_render', function() {
    setTimeout( function() {
      $( '.ginput_counter:not(:last-child)' ).remove();
    }, 100 );
  } );
} );



